// Импортируем глобальные переменные
@import "fonts";
@import "master";

// Utilities
html,
body {
  height: 100%;
  font-size: 14px;
  padding: 0;
  line-height: 21px;
  font-family: "Gotham", sans-serif;
  font-weight: 400;
  color: $black;
}
body {
  display: flex;
  flex-direction: column;
}
.main-container {
  flex: 1 0 auto;
}
.main-footer {
  flex-shrink: 0;
}

img {
  max-width: 100%;
}

p {
  margin-bottom: 10px;
}

a {
  color: $action;
  &:hover {
    color: $action-dark;
  }
}

// responsiveness limits
html,
body {
  min-width: 1280px;
}
.main-header,
.main-container,
.main-footer {
  min-width: 1280px;
}

.text-normal {
  font-family: "Gotham", sans-serif;
  font-size: 1rem;
  line-height: 21px;
  font-weight: 400;
}
.text-small {
  font-family: "Gotham", sans-serif;
  font-size: 0.8571rem;
  line-height: 17.9998px;
  font-weight: 400;
}
.text-subtitle {
  font-family: "Gotham", sans-serif;
  font-size: 1.2857rem;
  font-weight: bold;
  line-height: 27px;
}
.text-title {
  font-family: "Cera Round Pro", sans-serif;
  font-size: 2rem;
  font-weight: 900;
  line-height: 34px;
}

.title-line {
  position: relative;
  span {
    position: relative;
    z-index: 1;
    padding-right: 12px;
  }
  &::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 6px;
    height: 1px;
    width: 100%;
    background: $white;
    z-index: 0;
    opacity: 0.4;
  }
}

.badge-label {
  padding: 3px 10px;
  background-color: #fdcd25;
  border-radius: 10px;
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  line-height: 11px;
  color: rgba(0, 0, 0, 0.7);
  display: inline-block;
}

.mtp-50 {
  margin-top: 50px !important;
}
.mtp-40 {
  margin-top: 40px !important;
}
.mtp-30 {
  margin-top: 30px !important;
}
.mtp-10 {
  margin-top: 10px !important;
}

.mbp-50 {
  margin-bottom: 50px !important;
}
.mbp-40 {
  margin-bottom: 40px !important;
}
.mbp-30 {
  margin-bottom: 30px !important;
}
.mbp-20 {
  margin-bottom: 20px !important;
}
.mbp-10 {
  margin-bottom: 10px !important;
}
.mbp-5 {
  margin-bottom: 5px !important;
}

.mrp-10 {
  margin-right: 10px !important;
}
.mrp-20 {
  margin-right: 20px !important;
}
.mrp-30 {
  margin-right: 30px !important;
}

// Makes header dropdown to act on absolute even on mobile
.main-header__small-bar {
  .navbar-nav .dropdown-menu {
    position: absolute !important;
    float: none;
  }
}
// Fixes text vertical alignment
.header-osb-fix {
  margin-top: 1px;
}

.back-btn {
  display: flex;
  align-items: center;
  text-decoration: none !important;
  i {
    font-size: 15px;
    &::before {
      transform: rotate(90deg);
    }
  }
}

// Main styles
body {
  background-color: #f2f2f2;
  background-image: url("../assets/header-rounded-rec.svg");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: 100% 342px;
}

.search-form {
  position: relative;

  &__input {
    border: none !important;
    padding-right: 92px;
  }
  &__btn {
    position: absolute;
    right: -1px;
    top: 0;
    border-top-left-radius: 0 !important;
    border-bottom-left-radius: 0 !important;
    padding: 0 24px;
    height: 100%;
    border: none;

    i {
      font-size: 26px;
    }
  }
}

.sidebar-initial-spot {
  height: 100%;
  padding-bottom: 20px;
}

.main-nav-panel {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;

  &__item {
    text-align: center;
    &:not(:last-child) {
      margin-right: 80px;
    }

    a {
      color: $white;
      text-decoration: none;

      width: 30px;
      display: inline-flex;
      text-align: center;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    i {
      font-size: 30px;
    }
  }
}

.has-badge {
  position: relative;
  &__item {
    position: absolute;
    top: -1px;
    right: -5px;
    width: 14px;
    height: 14px;
    background-color: $accent;
    border-radius: 50%;
    border: 2px solid $main;
    box-sizing: content-box;

    font-size: 10px;
    line-height: 14px;
    color: $black;
    font-weight: bold;
  }
}

.main-header {
  &__top {
    background-color: $main;
    color: $white;
    padding-top: 10px;
    padding-bottom: 20px;
    &-content {
      &-center {
        width: 100%;
        margin-left: 30px;
        margin-right: 60px;
      }
      &-right {
        .main-nav-panel-wrap {
          margin-bottom: -8px;
        }
      }
    }

    &.fixed {
      position: fixed;
      z-index: 15;
      top: 0;
      width: 100%;
    }
  }
  &__bottom {
    color: $white;
    padding: 15px 0;
  }

  &__logo {
    &-wrap {
      width: 195px;
      flex: 0 0 auto;
    }
  }

  &__small-bar {
    min-height: 19px;
    margin-bottom: 20px;

    .nav-item {
      &:not(:last-child) {
        margin-right: 30px;
      }
      i {
        font-size: 16px;
        margin-right: 10px;
      }
    }
  }

  &__categories-nav {
    margin-left: 220px;
    .nav-item {
      &:not(:last-child) {
        margin-right: 50px;
      }
    }
  }
}
.main-footer {
  margin-top: 50px;
  color: $white;

  &__top {
    padding: 50px 0;
  }
  &__bottom {
    padding: 50px 0;
  }

  &__logo-wrap {
    margin-bottom: 30px;
  }
  &__social-links {
    margin-bottom: 38px;
  }
  &__social-link {
    font-size: 30px;
    color: $white !important;
    text-decoration: none !important;
    &:not(:last-child) {
      margin-right: 15px;
    }
  }
  &__divider {
    border-color: $white;
    opacity: 0.4;
    margin-bottom: 30px;
  }

  &__nav-title {
    display: block;
    margin-bottom: 20px;
  }
  &__nav-big {
    &-item {
      margin-bottom: 15px;
      a {
        color: $white-high;
      }
    }
  }
  &__nav-small {
    &-item {
      width: 46%;
      margin-bottom: 15px;
    }
  }
}

.main-container {
  &__content {
    &-wrap {
      padding: 0px 30px;
      background: #ffffff;
      border-radius: 30px;
      margin-bottom: 0px;
      margin-left: -30px;
      margin-right: -30px;
      position: relative;
    }
    padding-top: 40px;
    padding-bottom: 50px;
  }
}

.online-status-badge {
  font-style: italic;
  color: $black-secondary;
  font-size: 12px;
  margin-left: 10px;
  &__icon {
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background: #20da1c;
    display: inline-block;
    margin-bottom: 1px;
    margin-right: 5px !important;
  }
}

.sidebar {
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.05), rgba(0, 0, 0, 0.05)),
    #ffffff;
  border-radius: 20px;
  position: sticky;
  top: 20px;
  margin-bottom: 20px;
  margin-top: 20px;
  padding: 20px;
}

.benefits-card {
  padding: 0 20px;
  margin-bottom: 50px;

  &__icon {
    font-size: 86px;
  }
}

.banner-card {
  padding: 30px 20px;
  border: 1px solid $black-lines;
  border-radius: 20px;
  height: 250px;
  margin-bottom: 20px;

  background-repeat: no-repeat;
  background-size: cover;
  &--1 {
    background-position: top right;
    background-image: url("../assets/cbanner1.png");
  }
  &--2 {
    background-position: top center;
    background-image: url("../assets/cbanner2.png");
  }

  &__title {
    max-width: 60%;
    margin-bottom: 10px;
  }
  &__content {
    max-width: 50%;
    margin-bottom: 50px;
  }
}

.rating-stars {
  display: flex;
  align-items: center;
  &__star {
    &-wrap {
      // font-size: 0;
      display: inline-flex;
    }
    color: $black-lines;
    margin-right: 2px;
    font-size: 20px;
    &.active {
      color: $accent;
    }
  }
}

.banner-full {
  width: 100%;
  &__image {
    width: 100%;
    border-radius: 20px;
  }
}

.categories-panel {
  list-style: none;
  margin: 0;
  padding: 0;
  // overflow: hidden;
  display: flex;
  margin-bottom: 40px;
  li {
    &:not(:last-child) {
      margin-right: 20px;
    }
  }
}
.categories-panel {
  margin-bottom: 20px;
}
.applied-filters-panel {
  margin-bottom: calc(50px - 1rem); // 1rem margin for btns
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  .btn {
    border-radius: 50px;
    margin-bottom: 1rem;
    &:not(:last-child) {
      margin-right: 10px;
    }
    i {
      margin-left: 2px;
      font-size: 15px;
      vertical-align: text-bottom;
    }
  }
  &__sort {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 25%;
    span {
      margin-right: 5px;
    }
  }
}

.products-grid {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  margin-bottom: -50px;
  &__item {
    min-height: 400px;
    position: relative;
    width: 25%;
    margin-bottom: 50px;
  }

  &.products-grid--c-5 {
    .products-grid__item {
      width: 20%;
    }
  }
}

.product-card {
  &-wrap {
    padding: 0 20px;
    background: #ffffff;
    border-radius: 20px;
    transition: box-shadow 300ms;
    width: auto;

    // negative margin on wrap to make it outside of column
    margin-left: -10px;
    margin-right: -10px;
  }

  // padding: 20px 15px;
  width: 100%;
  position: relative;
  transition: 400ms;

  &__label {
    position: absolute;
    top: 0;
    left: 0;
    padding: 3px 10px;
    background-color: $accent;
    border-radius: 10px;
    font-style: normal;
    font-weight: 400;
    font-size: 10px;
    line-height: 11px;
    color: $black-subtext;
    display: inline-block;
  }
  &__image {
    width: 100%;
    border-radius: 20px;
  }
  &__category {
    display: block;
    text-decoration: none !important;
    margin-bottom: 0;
  }
  &__title {
    margin-bottom: 5px;
    font-size: 1.2857rem;
    font-weight: bold;
    line-height: 27px;
    text-decoration: none !important;
  }
  &__size {
    font-size: 14px;
    font-weight: 400;
    color: $black-subtext;
    position: relative;
    padding-left: 13px;
    white-space: nowrap;
    &::before {
      position: absolute;
      left: 0;
      content: "•";
      top: 50%;
      transform: translateY(-50%);
    }
  }
  &__actions {
    display: flex;
    justify-content: space-between;
    &-panel {
      display: none;
    }
  }
  &__rate {
    white-space: nowrap;
    margin-bottom: 18px;
    i {
      font-size: 10px;
    }
  }
  &__control-btn {
    border: none;
    background: none;
    outline: none !important;
    padding: 0;
    color: $action;
    text-decoration: none !important;
    &:not(:last-child) {
      margin-right: 10px;
    }
  }
  &__attributes {
    margin-top: 24px;
    display: none;
    &-item {
      width: 100%;
      display: flex;
      margin-bottom: 10px;
      i {
        margin-top: 2px;
        margin-right: 6px;
        font-size: 14px;
        color: $accent-dark;
      }
    }
  }
  &__discount {
    position: relative;
    font-size: 12px;
    line-height: 18px;
    color: $black-subtext;
    display: block;
    margin-bottom: -6px; // additional adjustment to main price
    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: -6px;
      height: 1px;
      width: 100%;
      background-color: $attention;
      transform: rotate(6.01deg);
      box-shadow: 0 -1px 0px 1px $white;
    }
  }
  &__footer {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
  &__out-of-stock {
    display: none;
  }
  &__added-btn {
    display: none;
  }

  &--added {
    .product-card__add-btn {
      display: none;
    }
    .product-card__added-btn {
      display: block;
    }
  }
  &--out-of-stock {
    .product-card__price {
      display: none;
    }
    .product-card__add-btn {
      display: none;
    }

    .product-card__out-of-stock {
      display: block;
    }

    .product-card__added-btn {
      display: none !important;
    }
  }
  &--favorite {
    .product-card__favorite-btn {
      i {
        color: $attention;
      }
    }
  }
}
// product-card--added product-card--out-of-stock product-card--favorite
.language-switcher {
  .nav-item {
    &:not(:last-child) {
      margin-right: 10px;
    }
  }
  &__item {
    color: $white-low !important;
    margin: 0;
    padding: 0 !important;
    &.active {
      color: $white !important;
    }
  }
}

.product-page {
  &__description {
    margin-left: -10px;
    padding-left: 10px;
    width: 75%;
  }
  &__actions-panel {
    display: flex;

    &-rate {
      white-space: nowrap;
      margin-right: 15px;
      i {
        font-size: 10px;
      }
    }

    &-controls {
      display: flex;
    }
    &-control-btn {
      font-size: 0.8571rem;
      line-height: 17.9998px;
      text-decoration: none !important;

      border: none;
      background: none;
      outline: none !important;
      padding: 0;
      color: $action;
      font-size: 0.8571rem;
      line-height: 17.9998px;
      text-decoration: none;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      flex-wrap: wrap;
      text-align: center;
      i {
        font-size: 15px;
        margin-right: 5px;
      }

      &:not(:last-child) {
        margin-right: 10px;
      }
    }
  }
  &__actions-divider {
    margin-top: 10px;
    margin-bottom: 20px;
  }
  &__attributes {
    display: flex;
    justify-content: space-between;
    &-item {
      width: 25%;
      flex: 0 0 auto;
      display: flex;
      flex-direction: column;
      i {
        font-size: 40px;
        color: $accent-dark;
        margin-bottom: 5px;
      }

      &-title {
        margin-bottom: 5px;
      }
    }
  }
  &__reviews {
    display: flex;

    &-summary {
      width: 25%;
      flex: 0 0 auto;
      padding-right: 15px;

      &-total {
        i {
          font-size: 30px;
          margin-right: 5px;
        }
        &-value {
          font-weight: bold;
          font-size: 18px;
          line-height: 27px;
          margin-left: 20px;
        }
      }
      &-divider {
        margin-top: 15px;
        margin-bottom: 25px;
      }
      &-score {
        display: flex;
        align-items: center;
        &:not(:last-child) {
          margin-bottom: 5px;
        }

        &-stars {
          margin-right: 5px;
          i {
            font-size: 10px;
            margin-right: 2px;
          }
        }
        &-bar {
          width: 100%;
          height: 4px;
          background-color: $black-lines;
          margin-right: 8px;
          border-radius: 50px;
          position: relative;
          &-inner {
            position: absolute;
            content: "";
            height: 4px;
            top: 0;
            left: 0;
            border-radius: 50px;
            background-color: $accent-dark;
          }
        }
        &-value {
          font-size: 12px;
          line-height: 18px;
          color: $black-secondary;
        }
      }
    }
    &-comments {
      width: 62.5%;
      padding-left: 10px;
    }
    &-comment {
      &:not(:last-child) {
        margin-bottom: 30px;
      }
      &-meta {
        display: flex;
        align-items: center;
        margin-bottom: 10px;
        &-rate {
          white-space: nowrap;
          margin-right: 5px;
          i {
            font-size: 10px;
          }
        }
      }
      &-text {
        margin-bottom: 20px;
      }
      &-uploads {
        display: flex;
        flex-wrap: wrap;
        &-item {
          width: 90px;
          height: 90px;
          border-radius: 10px;
          object-fit: cover;
          margin-bottom: 10px;
          &:not(:last-child) {
            margin-right: 10px;
          }
        }
      }
    }
  }
}
.product-page-sidebar {
  &__actions-panel {
    display: flex;
    justify-content: space-between;

    &-rate {
      white-space: nowrap;
      margin-right: 10px;
      i {
        font-size: 10px;
      }
    }

    &-controls {
      display: flex;
    }
    &-control-btn {
      font-size: 0.8571rem;
      line-height: 17.9998px;
      text-decoration: none !important;

      border: none;
      background: none;
      outline: none !important;
      padding: 0;
      color: $action;
      font-size: 0.8571rem;
      line-height: 17.9998px;
      text-decoration: none;
      display: inline-flex;
      align-items: center;
      i {
        font-size: 15px;
        margin-right: 5px;
      }

      &:not(:last-child) {
        margin-right: 10px;
      }
    }
  }
  &__actions-divider {
    margin-top: 10px;
    margin-bottom: 20px;
  }
  &__price {
    &-discount {
      position: relative;
      display: inline;
      font-weight: bold;
      color: $black-secondary;
      &::after {
        content: "";
        position: absolute;
        top: 50%;
        left: -4px;
        height: 3px;
        width: calc(100% + 6px);
        border-radius: 50px;
        background-color: $attention;
        transform: rotate(6.01deg);
        box-shadow: 0 -1px 0px 1px $white;
      }
    }
  }
  &__control-panel {
    background: $white;
    border-radius: 20px;
    padding: 10px;
    margin-left: -10px;
    margin-right: -10px;
    a {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      &.product-page-sidebar__notify-btn {
        display: none;
      }
      i {
        font-size: 20px;
        margin-left: 10px;
      }
    }
  }
  &__attributes {
    &-item {
      display: flex;
      align-items: flex-start;
      &:not(:last-child) {
        margin-bottom: 10px;
      }
      i {
        font-size: 20px;
        margin-right: 10px;
      }
    }
  }

  &__out-of-stock {
    display: none;
  }
  &__added-btn {
    &-wrap {
      display: none;
      border: 1px solid $black-lines;
      border-radius: 10px;
      justify-content: space-between !important;
    }
    font-size: 12px;
    padding-left: 14px;
    padding-right: 14px;
    flex: 0 0 auto;
    width: 65%;
    flex-wrap: nowrap !important;
    text-align: left;
    font-weight: bold;
    i {
      margin-left: 14px !important;
    }
  }

  &--added {
    .product-page-sidebar__add-btn {
      display: none;
    }
    .product-page-sidebar__added-btn-wrap {
      display: flex;
    }
  }
  &--out-of-stock {
    .product-page-sidebar__added-btn-wrap {
      display: none !important;
    }

    .product-page-sidebar__out-of-stock {
      display: block;
    }
    .product-page-sidebar__price {
      display: none;
    }
    .product-page-sidebar__notify-btn {
      display: flex !important;
    }
    .product-page-sidebar__add-btn {
      display: none;
    }
    .product-page-sidebar__phone-btn {
      display: none;
    }
  }
  &--favorite {
    .product-page-sidebar__favorite-btn {
      i {
        color: $attention !important;
      }
    }
  }
}

.cart-page-sidebar {
  &__control-panel {
    background: $white;
    border-radius: 20px;
    padding: 10px;
    margin-left: -10px;
    margin-right: -10px;
    a {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      &.product-page-sidebar__notify-btn {
        display: none;
      }
      i {
        font-size: 20px;
        margin-left: 10px;
      }
    }
  }
  &__attributes {
    &-item {
      display: flex;
      align-items: flex-start;
      &:not(:last-child) {
        margin-bottom: 10px;
      }
      i {
        font-size: 20px;
        margin-right: 10px;
      }
    }
  }
  &__divider {
    margin-top: 5px;
    margin-bottom: 5px;
  }
  &__coupon-form {
    .form-group {
      position: relative;
    }
    &-input {
      padding-right: 86px;
    }
    &-submit {
      position: absolute;
      right: 6px;
      top: 6px;

      background: none;
      outline: none !important;
      border: none;
      padding: 0;

      display: flex;
      align-items: center;

      i {
        margin-left: 5px;
        font-size: 15px;
