        margin-bottom: 20px;
      }

      display: block;
      width: 100%;
      border-radius: 20px;
      border: 1px solid $black-lines;
      object-fit: cover;
    }
    &-content {
      display: flex;
      flex-direction: column;
    }
    &-title {
      display: block;
      font-size: 18px;
      line-height: 27px;
      color: $action;
      margin-bottom: 10px;
    }
    &-text {
      margin-bottom: 20px;
    }
    &-video-btn {
      margin-top: auto;
    }
  }
}

