.shortened {
  max-height: 100px;
  overflow: hidden;
  position: relative;
  margin-bottom: 10px;
  &::after {
    position: absolute;
    content: "";
    bottom: 0;
    left: 0;
    width: 100%;
    background: linear-gradient(
      180deg,
      rgba(255, 255, 255, 0) 0%,
      #ffffff 100%
    );
    height: 100px;
    z-index: 2;
  }

  &.active {
    max-height: none;
    overflow: initial;
    &::after {
      display: none;
    }
  }
}

.promo-banners {
  &-wrap {
    margin-top: -40px;
  }

  display: grid;
  column-gap: 20px;
  row-gap: 20px;

  grid-template-areas:
    "big smallOne"
    "big smallTwo";

  &__item {
    position: relative;

    &:nth-child(1) {
      grid-area: big;
      margin-left: -30px;
      border-radius: 20px 0 20px 0;
      .promo-banners__item-image {
        height: 460px;
        border-radius: 20px 0 20px 0;
      }
      .promo-banners__item-image-wrap {
        &::after {
          border-radius: 20px 0 20px 0;
        }
      }
    }
    &:nth-child(2) {
      grid-area: smallOne;
      margin-top: 20px;
      .promo-banners__item-image {
        height: 210px;
      }
    }
    &:nth-child(3) {
      grid-area: smallTwo;
      .promo-banners__item-image {
        height: 210px;
      }
    }

    &-image {
      &-wrap {
        position: relative;
        &::after {
          position: absolute;
          content: "";
          width: 100%;
          height: 100%;
          left: 0;
          top: 0;
          border: 1px solid rgba(0, 0, 0, 0.15);
          border-radius: 20px;
        }
      }

      border-radius: 20px;
      width: 100%;
      object-fit: cover;
    }
  }
}

.main-nav-cards-grid {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  margin-bottom: 30px;

  &__item {
    &-wrap {
      width: 25%;
      margin-bottom: 20px;
      padding: 0 20px;

      a {
        text-decoration: none;
      }
    }

    position: relative;
    display: flex;
    flex-direction: column;
    margin-left: -10px;
    margin-right: -10px;
    padding: 20px;

    // border: 1px solid $black-lines;
    box-shadow: 0 0 0 1px $black-lines;
    border-radius: 20px;
    transition: 200ms;
    &:hover {
      background: $main;
      // background: linear-gradient(
      //   293.85deg,
      //   #ff35ba -17.55%,
      //   rgba(190, 53, 255, 0) 62.96%
      // );
      .main-nav-cards-grid__item-title {
        color: $white;
      }
      .main-nav-cards-grid__item-content {
        color: $white;
      }
      .main-nav-cards-grid__item-footer {
        color: $white;
      }
      &::after {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        border-radius: 20px;
        background: linear-gradient(
          293.85deg,
          #ff35ba -17.55%,
          rgba(190, 53, 255, 0) 62.96%
        );
      }
    }

    &-image {
      position: absolute;
      right: 0;
      top: 0;
      z-index: 1;
    }

    &-title {
      position: relative;
      z-index: 2;
      color: $black;
      margin-bottom: 10px;
    }
    &-content {
      position: relative;
      z-index: 2;
      color: $black-subtext;
      margin-bottom: 20px;
    }
    &-footer {
      position: relative;
      z-index: 2;
      color: $action;
      display: flex;
      align-items: flex-end;
      i {
        font-size: 15px;
        &::before {
          transform: rotate(-90deg);
        }
      }
    }
  }
}
.sub-nav-cards-grid {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;

  &__item {
    &-wrap {
      width: 20%;
      // width: 200px;
      margin-bottom: 20px;
      padding: 0 20px;
      &:nth-child(5n + 1) {
        .sub-nav-cards-grid__item {
          background: #ffe353;
        }
      }
      &:nth-child(5n + 2) {
        .sub-nav-cards-grid__item {
          background: #c5eff7;
        }
      }
      &:nth-child(5n + 3) {
        .sub-nav-cards-grid__item {
          background: #daa5ff;
        }
      }
      &:nth-child(5n + 4) {
        .sub-nav-cards-grid__item {
          background: #f1a9a0;
        }
      }
      &:nth-child(5n + 5) {
        .sub-nav-cards-grid__item {
          background: #81cfe0;
        }
      }

