}

.mobile-carousel {
  .slick-dots {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: row;

    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);

    padding: 4px 5px;
    border-radius: 50px;
    background: $white-low;

    li {
      display: flex;
      &:not(:last-child) {
        margin-right: 10px;
      }

      &.slick-active {
        button {
          background: $black-secondary;
        }
      }

      button {
        width: 8px;
        height: 8px;
        padding: 0;
        background: $black-lines;
        border-radius: 50%;
        border: none;
        outline: none !important;
        text-indent: -9999em;
      }
    }
  }
}

.blog-posts-grid {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;

  &__video-item {
    .blog-posts-grid__item-thumbnail-wrap {
      position: relative;
      &::after {
        pointer-events: none;
        content: "";
        position: absolute;
        border-radius: 20px;
