.interactive-map {
  display: flex;

  &__image {
    &-wrap {
      position: relative;
    }
    width: 890px;
    height: 500px;
    flex: 0 0 auto;
    max-width: unset;
  }

  &__panel {
    width: 370px;
    height: 500px;
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.05), rgba(0, 0, 0, 0.05)),
      #ffffff;
    border-radius: 20px;
    margin-left: 20px;
    padding: 20px;
    // display: flex; // set by js
    flex-direction: column;

    display: none;

    &-subtitle {
      font-weight: bold;
      font-size: 12px;
      line-height: 18px;
      text-transform: uppercase;
      color: $black-secondary;
      margin-bottom: 10px;
    }
    &-title {
      font-weight: bold;
      font-size: 18px;
      line-height: 27px;
      margin-bottom: 20px;
    }
    &-content {
      height: 100%;
      overflow: auto;
      color: $black-subtext;
      margin-bottom: 20px;
      padding-right: 8px;

      &::-webkit-scrollbar {
        width: 4px;
      }
      &::-webkit-scrollbar-track {
