.article-container {
  display: flex;
  margin-left: -10px;
  margin-right: -10px;

  &__main {
    width: 75%;
    flex: 0 0 75%;
    padding: 0 10px;
  }
  &__related {
    width: 25%;
    flex: 0 0 25%;
    padding: 0 10px;
  }

  &__content {
    p {
      width: 65.75%;
      margin-bottom: 20px;
    }
    img {
      margin-top: 10px;
      margin-bottom: 30px;
      border-radius: 20px;
    }

    h2 {
      font-style: normal;
      font-weight: bold;
      font-size: 18px;
      line-height: 27px;
      color: $black;
      margin-bottom: 20px;
    }
    h3 {
      font-style: normal;
      font-weight: bold;
      font-size: 12px;
      line-height: 18px;
      text-transform: uppercase;
      color: $black-secondary;
      margin-bottom: 20px;
    }
    ol {
      padding-left: 0;
      margin-bottom: 20px;
      list-style-type: none;
      counter-reset: ol-counter;
      li {
        &:not(:last-child) {
          margin-bottom: 20px;
        }
        &:before {
          content: counter(ol-counter) ". ";
          counter-increment: ol-counter;
        }
      }
    }
