.overlay-cdk {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: $black-subtext;
  padding: 50px;
  z-index: 9999;
  display: none;
  overflow: auto;
  &.active {
    display: block;
  }

  &__close-btn {
    position: absolute;
    top: 20px;
    right: 20px;
    outline: none !important;
    background: none;
    border: none;
    color: $white;
    font-size: 30px;
    z-index: 9999;
  }
  &__content {
    &-wrap {
      height: 100%;
    }
    margin-left: -30px;
    margin-right: -30px;
    height: 100%;
    border-radius: 30px;
    display: flex;
    flex-direction: column;

    &-video {
      &-wrap {
        height: 50%;
        margin-bottom: 20px;
      }
      height: 100%;
      border-radius: 30px;
    }
    &-feed {
      &-container {
        height: 50%;
        background: #fff;
        border-radius: 30px;
        padding: 30px;
      }
      &-header {
        display: flex;
        justify-content: space-between;
        margin-bottom: 20px;
      }
      &-grid {
        margin-left: -10px;
        margin-right: -10px;
        display: flex;
        flex-wrap: wrap;
        &-item {
          // display: flex;
          // flex-direction: column;
          width: 20%;
          padding: 10px;
          margin-bottom: 20px;
          &-image {
            display: block;
            border-radius: 20px;
            object-fit: cover;
            margin-bottom: 20px;
          }
          &-title {
            display: block;
            margin-bottom: 10px;
            font-size: 18px;
            line-height: 27px;

            .badge-label {
              vertical-align: text-bottom;
              margin-bottom: 2px;
            }
          }
        }
      }
    }
  }
}

.accordions-wrap {
  margin-bottom: 30px;
}

.accordion {
  &:not(:last-child) {
    margin-bottom: 10px;
  }
  &.opened {
    .accordion__toggle {
      i {
        &::before {
          transition: 400ms;
          transform: rotate(-180deg);
        }
      }
    }
  }
  &__toggle {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-decoration: none !important;
    margin-bottom: 20px;
    i {
      font-size: 15px;
      &::before {
        transition: 400ms;
        transform: rotate(0);
      }
    }
  }
  &__content {
    li {
      color: $white-high;
      &:not(:last-child) {
        margin-bottom: 15px;
      }
    }
  }
}

.mobile-drawer {
  position: fixed;
  z-index: 999;
  width: 100%;
  height: 100%;
  right: 0;
  top: 0;
  transform: translateX(100%);
  transition: transform 400ms;

  &__close-btn {
    position: fixed;
    z-index: 1000;
    top: 20px;
    right: 20px;
    outline: none !important;
    border: none;
    border-radius: 50%;
    color: $white;
    background: $main-dark;
    padding: 10px;
    display: flex;
    i {
      font-size: 15px;
    }
  }
  &__content {
    &-wrap {
      background: $main-dark;
      max-width: 300px;
      height: 100%;
      overflow: auto;
      margin-left: auto;
    }

    display: flex;
    flex-direction: column;
    min-height: 100%;
    &-header {
      padding: 20px 30px;
      background: $main;
    }
    &-footer {
      padding: 20px 30px;
    }
  }
  &__small-nav {
    margin-bottom: 40px;
    .nav-item {
      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }
  }
  &__search {
    margin-bottom: 15px;

    .search-form {
      &__btn {
        padding: 7px 15px;

        i {
          font-size: 15px;
        }
      }
    }
  }

  &__social-links {
    margin-bottom: 15px;
  }
  &__social-link {
    font-size: 30px;
    color: $white !important;
    text-decoration: none !important;
    &:not(:last-child) {
      margin-right: 15px;
    }
  }

  .nav-item {
    i {
      margin-right: 10px;
    }
  }
  .online-status-badge {
    margin-left: 25px;
  }
  .language-switcher {
    margin-left: 25px;
  }

  &.active {
    transform: translateX(0);
  }
  &.dark {
    transition: 400ms;
    background: $black-lines;
  }
}

@keyframes productAttributesAnimation {
  0% {
    opacity: 0;
    max-height: 0%;
  }
  75% {
    max-height: 100%;
    opacity: 1;
  }
}
